Variable font.
Usage:

  html { font-family: 'Inter', sans-serif; }
  @supports (font-variation-settings: normal) {
    html { font-family: 'Inter var', sans-serif; }
  }
*/





 .root {
  margin: 0px;
  padding: 0px;
  font-family: 'Inter', Arial, sans-serif;
  font-style: normal;
  background-color:  #000000;
  color: white;
}

.header__logo {
  width: 142.01px;
  height: 33.4px;
  margin-top: 0px;
  margin-left: 0px;
  margin-bottom: 40px;  
}

@media (max-width : 767px) {
  .header__logo {
    margin-left: 27px;
    margin-top: 28px;
  }
}    
@media (max-width : 374px) {
  .header__logo {
    width: 103.74px;
    height: 24.4px;
  }
}



.header {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  position: relative;
  width: 1000px;
  margin: auto;
  padding-top: 45px;
  margin-bottom: 0px;
  border-bottom: 1px rgba(255,255,255,0.3) solid;
}

@media (max-width : 1280px) {
  .header {
    width: 882px;
  }
}

@media (max-width : 1023px) {
  .header {
    width: 720px;
  }
}

@media (max-width : 767px) {
  .header {
    width: 100%;
    margin: 0px;
  }
}

.profile__name {
  font-family: Inter, Arial;
  font-weight: 500;
  font-size: 42px;
  line-height: 56px;
  margin: 0px;
  margin-top: 18px;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  text-align: end;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@media (max-width : 450px) {
  .profile__name {
    font-weight: 500;
    font-size: 27px;
    line-height: 56px;
    margin-top: 10px;
  }
}
.profile__avatar-box {
  position: relative;
  width: 120px;
  height: 120px; 
  border-radius: 50%;
  object-fit: cover;
  border-style: none;
}


.profile__avatar {
  width: 120px;
  height: 120px; 
  border-radius: 50%;
  object-fit: cover;
  border-style: none;
  background-repeat: no-repeat;
  background-position: center;
  
}


.profile__info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
  height: 100%;
  width: calc(100% - 120px - 50px - 150px);
  margin-left: 30px;
  margin-right:  20px;
}

@media (max-width : 767px) {
  .profile__info {
    margin: 0px;
    width: 100%;
    align-content: center;
    justify-items: center;
    align-items: center;
  }
}
.profile__first-line {
  display: flex ;
  flex-direction: row;
  justify-content: flex-start;
  align-items: baseline;
  width: 100%;
}

@media (max-width : 767px) {
  .profile__first-line {
    width: auto;
    max-width: 100%;
  }
}
.profile__description {
  font-family: Inter, Arial;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  margin: 0px;
  margin-top: 16px;
  margin-left: 1px;
}

@media (max-width : 450px) {
  .profile__description {
    font-size: 14px;
    line-height: 17px;
    margin-top: 0px;
    margin-left: 0px;
  }
}
.profile__add-button {
  width: 150px;
  height: 50px;
  margin-top: 39px;
  margin-left: auto;
  background-image: url(../../static/media/add.50cf4861.svg);
  background-color: black;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 22px;
  border-style: solid;
  border-width: 2px;
}

.profile__add-button:hover {
  opacity:0.5;
  cursor: pointer;
  transition: all 0.5s ease;
  position: relative;
  top: 1px;
}

.profile__add-button:focus {
  outline:none;
 }

@media (max-width : 767px) {
  .profile__add-button {
    width: 300px;
    margin: 0px;
    margin-top: 36px;
    background-size: 16px;
  }
}

@media (max-width : 550px) {
  .profile__add-button {
    width: 100%;
  }
}
.profile__avatar-edit-button {
  padding: 0px;
  position: absolute;
  border-style: none;
  top: -1px;
  left: -1px;
  width: 122px;
  height: 122px; 
  border-radius: 50%;
  opacity: 0
}

.profile__avatar-edit-button:hover {
  opacity: 1;
  background-image: url(../../static/media/edit.b9b65c7e.svg);
  background-color: rgba(0,0,0,0.8);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 26px;
  cursor: pointer;
  z-index: 0
}

.profile__edit-button{
  width: 24px;
  height: 24px;
  margin-left: 17px;
  background-image: url(../../static/media/edit.b9b65c7e.svg);
  background-color: black;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 10px;
  border-style: solid;
  border-width: 1px;
}

.profile__edit-button:hover {
  opacity:0.6;
  cursor: pointer;
  transition: all 0.5s ease;
  position: relative;
  top: 1px;
}
  
.profile__edit-button:focus {
  outline:none;
}

@media (max-width : 450px) {
  .profile__edit-button {
    width: 18px;
    height: 18px;
    background-size: 7.5px;
    margin-left: 5px;
  }
}
.profile {
  display: flex;
  flex-direction: row;
  justify-items: flex-start;
  position: relative;
  width: 1000px;
  margin: auto;
  margin-top: 36px;
  margin-bottom: 0px;
  height: auto;
  box-sizing: border-box;
}

@media (max-width : 1280px) {
  .profile {
    width: 882px;
  }
}

@media (max-width : 1023px) {
  .profile {
    width: 720px;
  }
}

@media (max-width : 767px) {
  .profile {
    width: calc(100% - 19px*2);
    min-width: 282px;
    margin: auto;
    margin-top: 38px;
    flex-direction: column;
    justify-items: center;
    align-items: center;
  }
}

.cards {
  display: grid;
  width: 1000px;
  margin: auto;
  margin-bottom: 0px;
  margin-top: 47px;
  grid-gap: 18px; 
  grid-template-columns: repeat(auto-fill, minmax(282px, 1fr));
}

@media (max-width : 1280px) {
  .cards {
    width: 882px;
    grid-gap: 18px;
  }
}

@media (max-width : 1023px) {
  .cards {
    width: 600px;
    margin-top: 36px;
    grid-gap: 18px;
  }
}

@media (max-width : 767px) {
  .cards {
    grid-gap: 20px;
  }
}

@media (max-width : 650px) {
  .cards {
    width: 282px;
  }
}


.card__name {
  font-family: Inter, Arial;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  color: black;
  margin-left: 21px;
  margin-top: 25px;
  width: 180px;
  height: 30px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.card__image {
  width: 100%;
  height: 282px;
  background-repeat: no-repeat;
  background-position: center;
}

.card__image:hover {
  cursor: pointer;
}

.card__like-button{
  background-image: url(../../static/media/like.e7a8906a.svg);
  position: relative;
  width: 20px;
  height: 18px;
  border-style: none;
  margin: 0px;
  padding: 0px;
  background-size: cover;
  background-color: transparent;
}

.card__like-button:hover {
  opacity:0.5;
  cursor: pointer;
  transition: all 0.5s ease;
  top: 1px;
}

.card__like-amount {
  font-family: Inter, Arial;
  font-style: normal;
  font-size: 13px;
  line-height: 29px;
  color: black;
  line-height: 16px;
  text-align: center;
  margin: 0px;
  margin-top: 3px;
}

.card__like-block{
  height: 34px;
  width: 20px;
  margin-top: 21px;
  margin-right: 21px;
  display: flexbox;
  flex-direction: column;
}



.card__like-button_active {
  background-image: url(../../static/media/active-like.b1216740.svg);
}

.card__remove-button{
  z-index: -1;
  position: absolute;
  top: 18px;
  right: 15px;
  width: 18px;
  height: 19.3px;
  background-image: url(../../static/media/remove.66807b14.svg);
  background-size: cover;
  background-color: transparent;
  border-style: none;
}

.card__remove-button:hover {
  opacity:0.5;
  cursor: pointer;
  transition: all 0.5s ease;
  top: 19px;
}
.card__remove-button_active{
  z-index: 0;
}

.card{
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: white;
  width: 310px;
  height: 361px;
  border-radius: 10px;
  overflow: hidden;
  flex-wrap: wrap;
}

@media (max-width : 1280px) {
  .card {
    width: 282px;
  }
}  



.footer__copyright {
