.popup{
  display: flex;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s, visibility 0s linear 0.3s;
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
  align-content: center;
  padding: 0px;
}

.popup:hover {
  cursor: pointer;
}

.popup_opened{
  display: flex;
  visibility:visible;
  opacity: 1;
  transition: opacity 0.5s, visibility 0s;
}
  
  


